        height: 51px;
        background: #005FA3;

        border-radius: 10px;

        font-family: 'lato', sans-serif;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 138%;
        color: #FFFFFF;
        background-color: #005FA3;
        text-align: center;
        user-select: none;
        cursor: pointer;
