.tabs {
    padding-top: 66px;
    &__items {
        display: flex; }
    &__item {
        margin: 0 auto;
        display: flex;
        justify-content: center;
        align-items: center;
        text-transform: uppercase;
        color: #ffffff;
        text-decoration: none;
        &:nth-child(1) {
            width: 225.47px;
            height: 60px;
            background: #EBEBEB;
            color: #000000;
            &:hover {
                background: #005FA3;
                color: white; } }
        &:nth-child(2) {
            width: 225.47px;
            height: 60px;
            background: #EBEBEB;
            color: #000000;
            &:hover {
                background: #005FA3;
                color: white; } } }
    a {
        display: flex; }
    &__block {
        display: none;
        &:target {
            display: block; } }
    &:nth-child(1) {
        font-size: 20px;
        color: white; }
    &:nth-child(2) {
        font-size: 20px;
        color: white; } }


